/* SignUpPage.css */
.signup-page-container {
  display: flex;
  height: 100vh;
  width: 100vw;
}

.library-image-container {
  flex: 1;
  background-color: #f5f5f5;
  overflow: hidden;
}

.library-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.signup-container {
  flex: 1;
  position: relative;
  background-color: #AD8C73;
}

.signup-form {
  position: relative;
  width: 100%;
  height: 100%;
}

.signup-title {
  position: absolute;
  width: 141px;
  height: 48px;
  left: 666px;
  top: 8px;
  font-family: 'Kavoon', cursive;
  font-size: 32px;
  line-height: 40px;
  color: #383030;
}

.form-group {
  position: absolute;
}

.full-name-label {
  position: absolute;
  width: 196px;
  height: 31px;
  left: 492px;
  top: 51px;
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
}

