.input-container {
  position: absolute;
  width: 354px;
  height: 55px;
}

.input-field {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #FFFCFC;
  border-radius: 28px;
  border: none;
  padding-left: 50px;
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.input-field::placeholder {
  color: #BEBEBE;
}

.input-icon {
  position: absolute;
  z-index: 1;
}

