.login-page-container {
  display: flex;
  height: 100vh;
  width: 100vw;
}

.library-image-container {
  flex: 1;
  background-color: #f5f5f5;
  overflow: hidden;
}

.library-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.login-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #AD8C73;
}

.login-form {
  width: 354px;
  display: flex;
  flex-direction: column;
}

.login-title {
  font-family: 'Kavoon', cursive;
  font-size: 32px;
  color: #383030;
  text-align: center;
  margin-bottom: 40px;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px; /* Increased space between form groups */
}

.password-group {
  margin-bottom: 20px; /* Specific spacing for password group */
}

.input-label {
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: #000000;
  margin-bottom: 10px;
  padding-left: 15px;
}

.input-container {
  position: relative;
}

.input-icon {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
}

.input-field {
  width: 100%;
  height: 55px;
  padding-left: 50px;
  background: #FFFCFC;
  border-radius: 28px;
  border: none;
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 16px;
}

.input-field::placeholder {
  color: #BEBEBE;
}

.register-link {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 30px 0;
}

.register-text {
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: #000000;
}

.create-account {
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: #BEBEBE;
  text-decoration: none;
}

.login-button-container {
  display: flex;
  justify-content: center;
}

.login-button {
  width: 174px;
  height: 48px;
  background: #596288;
  border-radius: 28px;
  border: none;
  font-family: 'Karla', sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: #000000;
  cursor: pointer;
}

.login-button:hover {
  background: #4a5570;
}